@import "../utils/_variables";
@import "../utils/_mixins";
@import "../utils/_functions";


form {
 // @include marginPx(null,20, auto);
  @include margins(20px, auto);
  max-width: convertPxToRem(550);

  p {
    font-size: convertPxToRem(12);
  }
}

input {
  @include display(block, $full);
  @include border($color-secondary);
  font-size: convertPxToRem(12);
  padding: convertPxToRem(15);
  margin-bottom: convertPxToRem(20);
  color: $color-primary;
  outline: none;
}
textarea {
  @extend input;
  resize: none;
}

